#modal-background {
    position: fixed;
}
#modal-wrapper {
    position: absolute;
    top: 20%;
    left: auto;
    width: 95vw;
}
#modal-container {
    height: 50vh;
    align-self: center;
    display: grid;
    grid-template-columns: 1fr 3fr;
    gap: 2rem;
    align-items: stretch;
    justify-content: center;
}

#modal-image {
    display: flex;
    align-content: center;
    height: 100%;
}

.big-thumbnail {
    height: auto;
    width: auto;
    margin: 2rem;
    box-shadow: 0 1px 3px 0 #d4d4d5, 0 0 0 1px #d4d4d5;
}

#modal-content {
    height: 40vh;
    display: flex;
    flex-flow: column;
    margin-left: 4rem;
    align-self: center;
}

/* .modal-actions {

