#modal-description {
    margin-bottom: auto;
    height: 70%;
    overflow: hidden;
    text-overflow: ellipsis;
    /* -webkit-line-clamp: 7; */
    display: -webkit-box;
    -webkit-box-orient: vertical;
    /* line-height: 1.5rem;
